.Home{
  height: 100%;
}


.loader {
  border: 5px solid #f3f3f3; 
  border-top: 5px solid #ff6347; 
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 1s linear infinite;
  text-align: center;
  margin: 20px auto; 
  display: block; 
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}