.search-bar {
  display: flex;
  justify-content: center;
  margin: 30px 0;
}

.search-bar input {
  padding: 10px;
  border-radius: 30px;
  border: 1px solid #ff6347;
  outline: none;
  font-size: 1rem;
  background-color: #333;
  color: white;
  width: 300px;
  transition: all 0.3s ease-in-out;
}

.search-bar input:focus {
  border-color: #ff4500;
  box-shadow: 0 0 10px rgba(255, 69, 0, 0.6);
}

.search-bar button {
  padding: 10px 20px;
  border-radius: 30px;
  background-color: #ff6347;
  border: none;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  margin-left: 10px;
}

.search-bar button:hover {
  background-color: #ff4500;
}
