.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 30px;
}

.pagination button {
  padding: 10px 20px;
  border-radius: 30px;
  background-color: #ff6347;
  border: none;
  color: white;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.pagination button:disabled {
  background-color: #444;
  cursor: not-allowed;
}

.pagination button:hover:not(:disabled) {
  background-color: #ff4500;
}
