header {
  background-color: #121212;
  padding: 20px 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 40px;
}

nav li {
  font-size: 1.2rem;
}

nav a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease-in-out;
}

nav a:hover {
  color: #ff6347;
}
